<template>
  <div class="w-textarea" ref="wTextarea">
    <div class="w-textarea_input" ref="wTextareaContent" :id="contentId" @click="inputClick($event)" @focus="isLocked = true" @blur="isLocked = false"
      @keydown.delete="handleDelete($event)" @input="handleInput($event.target)"></div>
    <div class="w-textarea_tools" v-if="tools.length > 0 || maxlength">
      <button class="w-textarea_tools__item" v-for="item in tools" :key="item.type" @click="openTagDialog(item.type)">{{item.text}}</button>
      <span :class="['w-textarea_tools__text',
        count.num < 0 ? '__danger' : '']" v-if="maxlength">{{count.text}}</span>
    </div>
    <div class="w-textarea_dialog">
      <slot></slot>
    </div>
    {{state}}
  </div>
</template>

